html {
  height: 100%; }

body {
  height: 100%;
  background: linear-gradient(to bottom left, #98D8FC, #63ABE5);
  background-attachment: fixed; }

.movie-list {
  opacity: 1;
  transition: opacity .25s; }

.container {
  max-width: 480px;
  padding: 0 16px; }

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 24px auto;
  color: white; }
  .navbar .classification {
    font-size: 24px; }

.movie-card {
  padding-bottom: 48px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 10px 10px 20px #62A5D9; }
  .movie-card.preview {
    text-align: center;
    color: #387F7F; }
    .movie-card.preview .movie-card-inner {
      overflow: hidden;
      border-radius: 8px; }
    .movie-card.preview .preview-image {
      position: relative;
      overflow: hidden;
      padding-top: 105%; }
      .movie-card.preview .preview-image img {
        display: block;
        position: absolute;
        top: 0; }
    .movie-card.preview .info {
      margin: 32px 16px; }
    .movie-card.preview .title {
      margin: 0 auto 16px;
      font-size: 30px;
      font-weight: 500; }
    .movie-card.preview .description {
      font-weight: 500; }
  .movie-card.detail {
    margin-top: 72px; }
    .movie-card.detail .preview-video {
      overflow: hidden;
      position: relative;
      top: -12px;
      margin: 0 12px;
      padding-top: 56.25%;
      border-radius: 8px; }
      .movie-card.detail .preview-video iframe {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%; }
    .movie-card.detail .info {
      padding: 24px 32px;
      color: #63ABE5;
      font-size: 13px;
      font-weight: 500; }
      .movie-card.detail .info .info-row {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        margin-bottom: 8px; }
        .movie-card.detail .info .info-row .title {
          margin: 0;
          font-size: 18px;
          font-weight: 500;
          color: #387F7F; }
        .movie-card.detail .info .info-row .score {
          color: #727272;
          font-size: 18px;
          font-weight: 300; }
          .movie-card.detail .info .info-row .score span {
            color: #63ABE5;
            font-size: 32px; }
    .movie-card.detail .description {
      padding: 24px 32px 48px;
      font-size: 13px;
      font-weight: 500;
      line-height: 1.717;
      color: #727272; }

.slider-control {
  display: flex;
  justify-content: center;
  margin: 24px 0; }
  .slider-control .dot {
    width: 6px;
    height: 6px;
    margin: 12px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 50%; }
    .slider-control .dot.is-active {
      background-color: rgba(0, 0, 0, 0.5); }

.button {
  cursor: pointer;
  opacity: 1;
  transition: opacity .25s; }
  .button:hover {
    opacity: .7; }
  .button.call-to-action {
    display: block;
    min-width: 160px;
    width: 50%;
    margin: -40px auto 0;
    padding: 24px 36px;
    text-align: center;
    font-weight: 500;
    color: #70B4E6;
    background-color: #D7EBF0;
    border-radius: 8px;
    box-shadow: 0 10px 20px #62A5D9;
    transition: background-color .25s; }
    .button.call-to-action:hover {
      opacity: 1;
      background-color: #e2f1f4; }
  .button.back {
    width: 24px;
    height: 24px;
    text-indent: -9999px;
    background: resolve("icon-back.svg") no-repeat;
    background-size: contain; }
  .button.search {
    width: 24px;
    height: 24px;
    text-indent: -9999px;
    background: resolve("icon-search.svg") no-repeat;
    background-size: contain; }
  .button.close {
    width: 24px;
    height: 24px;
    margin: 36px auto;
    text-indent: -9999px;
    background: resolve("icon-close.svg") no-repeat;
    background-size: contain; }

.modal {
  display: none;
  width: 100%;
  position: absolute;
  top: 0; }
  .modal.fade {
    opacity: 0;
    transition: opacity .25s; }
    .modal.fade .movie-detail.container {
      opacity: 0;
      transform: translateY(10%);
      transition: opacity 1s, transform .5s; }
  .modal.in {
    opacity: 1; }
    .modal.in .movie-detail.container {
      opacity: 1;
      transform: translateY(0%); }

.modal-open .movie-list {
  opacity: 0; }

.modal-open .modal {
  display: block; }

