$color-title: #387F7F;
$color-main-light: #98D8FC;
$color-main: #63ABE5;
$color-text: #727272;
$color-shadow: #62A5D9;
$color-block-button: #D7EBF0;

html {
  height: 100%; }
body {
  height: 100%;
  background: linear-gradient(to bottom left, $color-main-light, $color-main);
  background-attachment: fixed; }

.movie-list {
  opacity: 1;
  transition: opacity .25s; }

.container {
  max-width: 480px;
  padding: 0 16px; }

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 24px auto;
  color: white;

  .classification {
    font-size: 24px; } }

.movie-card {
  padding-bottom: 48px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 10px 10px 20px $color-shadow;

  &.preview {
    text-align: center;
    color: $color-title;

    .movie-card-inner {
      overflow: hidden;
      border-radius: 8px; }

    .preview-image {
      position: relative;
      overflow: hidden;
      padding-top: 105%;

      img {
        display: block;
        position: absolute;
        top: 0; } }

    .info {
      margin: 32px 16px; }

    .title {
      margin: 0 auto 16px;
      font-size: 30px;
      font-weight: 500; }

    .description {
      font-weight: 500; } }

  &.detail {
    margin-top: 72px;

    .preview-video {
      overflow: hidden;
      position: relative;
      top: -12px;
      margin: 0 12px;
      padding-top: 56.25%;
      border-radius: 8px;

      iframe {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%; } }

    .info {
      padding: 24px 32px;
      color: $color-main;
      font-size: 13px;
      font-weight: 500;

      .info-row {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        margin-bottom: 8px;

        .title {
          margin: 0;
          font-size: 18px;
          font-weight: 500;
          color: $color-title; }

        .score {
          color: $color-text;
          font-size: 18px;
          font-weight: 300;

          span {
            color: $color-main;
            font-size: 32px; } } } }

    .description {
      padding: 24px 32px 48px;
      font-size: 13px;
      font-weight: 500;
      line-height: 1.717;
      color: $color-text; } } }

.slider-control {
  display: flex;
  justify-content: center;
  margin: 24px 0;

  .dot {
    width: 6px;
    height: 6px;
    margin: 12px;
    background-color: rgba(255, 255, 255, .5);
    border-radius: 50%;

    &.is-active {
      background-color: rgba(0, 0, 0, .5); } } }

.button {
  cursor: pointer;
  opacity: 1;
  transition: opacity .25s;

  &:hover {
    opacity: .7; }

  &.call-to-action {
    display: block;
    min-width: 160px;
    width: 50%;
    margin: -40px auto 0;
    padding: 24px 36px;
    text-align: center;
    font-weight: 500;
    color: #70B4E6;
    background-color: $color-block-button;
    border-radius: 8px;
    box-shadow: 0 10px 20px $color-shadow;
    transition: background-color .25s;

    &:hover {
      opacity: 1;
      background-color: lighten($color-block-button, 3%); } }

  &.back {
    width: 24px;
    height: 24px;
    text-indent: -9999px;
    background: resolve('icon-back.svg') no-repeat;
    background-size: contain; }

  &.search {
    width: 24px;
    height: 24px;
    text-indent: -9999px;
    background: resolve('icon-search.svg') no-repeat;
    background-size: contain; }

  &.close {
    width: 24px;
    height: 24px;
    margin: 36px auto;
    text-indent: -9999px;
    background: resolve('icon-close.svg') no-repeat;
    background-size: contain; } }

.modal {
  display: none;
  width: 100%;
  position: absolute;
  top: 0;

  &.fade {
    opacity: 0;
    transition: opacity .25s;

    .movie-detail.container {
      opacity: 0;
      transform: translateY(10%);
      transition: opacity 1s, transform .5s; } }

  &.in {
    opacity: 1;

    .movie-detail.container {
      opacity: 1;
      transform: translateY(0%); } } }

.modal-open {

  .movie-list {
    opacity: 0; }

  .modal {
    display: block; } }
